@import url(https://fonts.googleapis.com/css?family=Inconsolata:700);
* {
  margin:0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.home {
    background:url(/student-corner/static/media/hone02.5467cd01.jpg) no-repeat center;
    background-size: cover;
    background-attachment: fixed;
    height:100vh;
    
}
.navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: aliceblue;
    opacity: 0.8;
    height:60px;
    align-items: center;
    width:100vw;
    position: fixed;
   
}

.nav-logo {
    margin-left:200px;
}

.nav-right {
    display: flex;
    flex-direction: row;
    margin-right:200px;
}

.nav-right > h3{
    font-weight: 1;
    margin-right : 50px;
}
.registration {
    height:500px;
    background-color: #3c4554;
}

.registration-section {
    background-color:white;
    margin-right:130px;
    margin-left:130px;
    height:500px;
    box-shadow: -20px 4px 20px -6px rgba(83, 82, 82, 0.2);
}

.registration-info {
    display: flex;
    flex-direction: row;
}

.registration-info-1 {
    display: flex;
    flex-direction:column ;
    padding:20px;
}

.registration-info-1-1 {
    display: flex;
    flex-direction: row;
}

.registration-info-1-1-1 {
    padding:40px;
    width:370px;;
}

.registration-info-1-1-1 > h2 {
    font-size:17px;
}

.registration-info-1-1-1 >p {
    font-weight: 1;
    padding:10px;
    text-align: justify;
}

.registration-form {
    background-color: #e84c3d ;
    width:900px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height:500px;
}

.registration-form > h2 {
    font-weight: 2;
    margin:30px;
}

.registration-form > input {
    height:50px;
    border: none;
    outline-width:0;
    width:250px;
    padding-right:20px;
}

::-webkit-input-placeholder {
    padding-left: 20px;;
}

:-ms-input-placeholder {
    padding-left: 20px;;
}

::-ms-input-placeholder {
    padding-left: 20px;;
}

::placeholder {
    padding-left: 20px;;
}

.btn {
     height:50px;
     width:150px;
    background-color: cornflowerblue;
    border:none;
    outline-width: 0;
    cursor: pointer;
    margin-top: 20px;
    color:white;
    font-size: 14px;;
}

.btn:hover {
    background-color:rgb(59, 93, 155);
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top:80px;
}

.container > h1 {
    font-weight: 1;;
}

.container > h1 > span {
    font-weight: 500;
}

.container > span > i {
    color:grey;
    
}

hr {

    height:0.1px;
    border:none;
    border-color: grey;
}

.btn {
    border-radius: 2px;
}
.footer-section {
    height: 350px;
    background-color: #3c4554;
    color:white;
    display: flex;
    padding:50px;
    padding-right:100px;
    justify-content: center;
}

.aboutus {
    width:500px;
    padding-right: 120px;
}

.aboutus > h2 {
    font-size: 16px;;
}

.aboutus > h1 {
    font-weight: 400;
    padding-top:10px;
}

.aboutus > p {
    text-align: justify;
    color:rgb(214, 208, 208);
    font-weight: 1;
    padding-top:20px;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.header h1 {
    color: white;
    margin-left: 30vh;
}
.header{
    margin-top: -10vh;
}
.show-menu{
    display: none;
}

body {
    background-color: #353E49;
}

.header h3 {
    color: rgb(184, 175, 175);
    font-weight: lighter;
    margin-left: 30vh;
    padding-top: 1vh;
}

.menu li {
    list-style: none;
    color: white;
    padding-left: 40px;
    padding-top: 1.3rem;
    font-size: 1.1rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.menu {
    background-color: #272f38;
    width: 60vh;
    height: 70vh;
    display: flex;
    flex-direction: column;
    margin-left: 30vh;
    margin-top: 5vh;
    border-radius: 0.5rem;
}

.sidebar {
    float: left;
    margin: 7.5% 0;
    width: 32%;
    display: block;
}

.content {
    background: #fff;
    float: left;
    height: 500vh;
    position: relative;
    width: 50%;
    z-index: 2;
}

.menu li:hover {
    color: red;
    font-size: 1.5rem;
    border-radius: 0.5rem;
    font-family: 'Inconsolata';
}

h1:hover {
    color: red;
}

h3:hover {
    color: red;
}

.container {
    position: absolute;
    margin-left: 35vh;
    padding-top: 3vh;
}

input {
    width: 25vh;
    height: 5vh;
    font-size: 1.1rem;
    border-radius: 10px;
    text-align: center;
    font-family: 'Inconsolata';
    background-color: #353E49;
    color: white;
    border-color: white;
}

::-webkit-input-placeholder {
    color: white;
    font-style: italic;
}

:-ms-input-placeholder {
    color: white;
    font-style: italic;
}

::-ms-input-placeholder {
    color: white;
    font-style: italic;
}

::placeholder {
    color: white;
    font-style: italic;
}

@media only screen and (max-width:768px)
{
    .content{
        position: absolute;
        margin-top: 25vh;
        width: 200vh;

    }
    .menu{
        display: none;
    }
    .header{
        position: fixed;
        display: flex;
        padding-top: 4rem;
        background-color: #272f38;
        width: 100%;
        justify-content: center;
        height: 30%;
        flex-direction: column;
        align-items: center
    }
    .header h1{
        font-size: 1.5rem;     
        margin-left: 6%; 
        margin-top: -20%;

    }
    .header h3{
        font-size: 1.2rem;
        margin-left: 7%;
    }
    input{
        display: none;
    }
    .line {
        width: 20px;
        height: 2px;
        background: white;
        margin: 2px;
    }
    .hamburger {
        position: absolute;
        cursor: pointer;
        left: 63%;
        top: 20%;
        transform: translate(-5%, -50%);
        z-index: 2;
    }
    .show-menu{
        display: inherit;
        position: absolute;
        color:white;
        font-size: 0.6rem;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        left: 45%;
        top: 19%;
    }
    
}
@media only screen and (min-width: 768px)
{
    .menu{
        margin-left: 15%;
    }
    .header h1{
        margin-left: 15%;
        margin-top: 4vh;
        font-size: 1.5rem;
    }
    .header h3{
        margin-left: 15%;
    }
    input
    {
        margin-left: -27vh;        
    }
}
